.sec-contact {
  padding: 4.2307692308vw 0 5.3846153846vw;
}
.sec-contact .list {
  width: 60vw;
  margin: 0 auto 3.0769230769vw;
}
.sec-contact .list .box {
  line-height: 1.6em;
  margin-bottom: 1.6923076923vw;
}
.sec-contact .list .box:last-child {
  margin-bottom: 0;
}
.sec-contact .list .box .ttl {
  font-size: 1.2307692308vw;
  letter-spacing: 0.05em;
  margin-bottom: 0.9230769231vw;
}
.sec-contact .list .box .ttl span {
  font-size: 0.9230769231vw;
  color: #ffffff;
  background: #b6966c;
  font-weight: bold;
  padding: 0.3076923077vw 0.5384615385vw;
  margin-left: 0.7692307692vw;
}
.sec-contact .list .box .input input[type=text],
.sec-contact .list .box .input input[type=email],
.sec-contact .list .box .input input[type=url],
.sec-contact .list .box .input input[type=tel],
.sec-contact .list .box .input textarea {
  font-size: 1.0769230769vw;
  background: #faf8f5;
  width: 100%;
  border: none;
  padding: 1em;
  appearance: none;
}
.sec-contact .list .box .input select {
  width: 100%;
  height: 3.4615384615vw;
  background: #faf8f5;
  border: 1px solid #ccc;
  border-radius: 0;
}
.sec-contact .list .box .input ::placeholder {
  font-size: 1.0769230769vw;
  color: #909090;
}
.sec-contact .list .box .input small {
  font-size: 0.9230769231vw;
  color: #666666;
  font-weight: 400;
  padding-top: 0.5384615385vw;
}
.sec-contact button {
  background: #fff;
  cursor: pointer;
  appearance: none;
}
@media only screen and (max-width: 767px) {
  .sec-contact {
    padding: 8vw 0 12vw;
  }
  .sec-contact .list {
    width: 100%;
    margin-bottom: 5.3333333333vw;
  }
  .sec-contact .list .box {
    margin-bottom: 5.8666666667vw;
  }
  .sec-contact .list .box .ttl {
    font-size: 3.7333333333vw;
    margin-bottom: 2.1333333333vw;
  }
  .sec-contact .list .box .ttl span {
    font-size: 2.9333333333vw;
    padding: 0.5333333333vw 1.6vw;
  }
  .sec-contact .list .box .input input[type=text],
  .sec-contact .list .box .input input[type=email],
  .sec-contact .list .box .input input[type=url],
  .sec-contact .list .box .input input[type=tel],
  .sec-contact .list .box .input textarea {
    font-size: 3.7333333333vw;
  }
  .sec-contact .list .box .input select {
    height: 12vw;
  }
  .sec-contact .list .box .input ::placeholder {
    font-size: 3.7333333333vw;
  }
}

.sec-confirm {
  padding: 4.2307692308vw 0 5.3846153846vw;
}
.sec-confirm .ttl {
  font-size: 1.3846153846vw;
  font-weight: bold;
  margin-bottom: 1.5384615385vw;
}
.sec-confirm .txt {
  font-size: 1.0769230769vw;
  margin-bottom: 3.0769230769vw;
}
.sec-confirm .inner {
  width: 60vw;
  margin: 0 auto;
}
.sec-confirm .inner table.formTable {
  width: 100%;
  margin-bottom: 3.8461538462vw;
}
.sec-confirm .inner table.formTable tr {
  border-bottom: 1px solid #acacac;
}
.sec-confirm .inner table.formTable tr th {
  width: 28%;
  font-size: 1.0769230769vw;
  font-weight: bold;
  padding: 1.9230769231vw 1.1538461538vw 1.9230769231vw 0;
  text-align: left;
}
.sec-confirm .inner table.formTable tr td {
  width: 72%;
  font-size: 1.0769230769vw;
  padding: 1.9230769231vw 0;
  text-align: left;
}
.sec-confirm .inner button {
  background: #fff;
  cursor: pointer;
  appearance: none;
}
.sec-confirm .inner button:hover {
  color: #fff;
  background: #251e1c;
}
.sec-confirm .inner button.back {
  background: #efefef;
}
.sec-confirm .inner button.back:hover {
  color: #251e1c;
}
.sec-confirm .inner .box-btn button {
  margin: 0 1.5384615385vw;
}
.sec-confirm .inner.error p {
  font-size: 1.0769230769vw;
}
.sec-confirm .inner.error .error_messe {
  color: red;
}
@media only screen and (max-width: 767px) {
  .sec-confirm {
    padding: 8vw 0 12vw;
  }
  .sec-confirm .ttl {
    font-size: 4.2666666667vw;
    margin-bottom: 4vw;
  }
  .sec-confirm .txt {
    font-size: 3.2vw;
    margin-bottom: 5.3333333333vw;
  }
  .sec-confirm .inner {
    width: 100%;
  }
  .sec-confirm .inner table.formTable {
    display: block;
    margin-bottom: 8vw;
  }
  .sec-confirm .inner table.formTable tbody {
    display: block;
    width: 100%;
  }
  .sec-confirm .inner table.formTable tr {
    display: block;
    width: 100%;
  }
  .sec-confirm .inner table.formTable tr th {
    display: block;
    width: 100%;
    font-size: 3.7333333333vw;
    padding: 4.5333333333vw 0 1.8666666667vw;
  }
  .sec-confirm .inner table.formTable tr td {
    display: block;
    width: 100%;
    font-size: 3.7333333333vw;
    padding: 0 0 4.5333333333vw;
  }
  .sec-confirm .inner .box-btn button {
    margin-bottom: 2.6666666667vw;
  }
  .sec-confirm .inner .box-btn button:last-child {
    margin-bottom: 0;
  }
  .sec-confirm .inner.error p {
    font-size: 3.7333333333vw;
  }
}

